import { getNewsDetail } from '../../request/news_detail.js'
import { formatDateMixin } from '../../utils/mixin.js'

export default {
	data() {
		return {
			id: 0,
			detail: {}
		}
	},
	onLoad(options) {
		this.id = options.id
		this.getNewsDetail()
	},
	methods: {
		async getNewsDetail() {
			const res = await getNewsDetail(this.id)
			this.detail = res.message[0]
		}
	},
	mixins: [formatDateMixin]
}
